import React, { useState } from 'react';
import { MinimunStepGenericProps, StepGeneric } from './StepGeneric';
import { StepValueTile } from './StepValueTile';


const CAPACITIES = new Map<string, { price: number }>([
  ['16', { price: 18 }],
  ['24', { price: 24 }],
  ['32', { price: 32 }]
]);

interface StepCapacityProps extends MinimunStepGenericProps {
  onChange?: (value: [val: string, price: number]) => void
}

export function StepCapacity(props: StepCapacityProps) {
  const [value, setValue] = useState<string>('');

  function selectValue(value: string): void {
    const val = CAPACITIES.get(value);
    if (!val) return;
    
    setValue(value);
    if (props.onChange)
      props.onChange([value, val.price]);
    if (props.onNext)
      props.onNext();
  }

  function getValueLabel(): string {
    return value + ' filières';
  }

  function getPriceLabel(valueKey: string = value): string {
    const val = CAPACITIES.get(valueKey);
    return val ? val.price + '€' : '';
  }

  return (
    <StepGeneric {...props} stepClassName='capacity' typeLabel='Capacité' introLabel='Capacité du support' valueLabel={getValueLabel} priceLabel={getPriceLabel}>
      {Array.from(CAPACITIES, ([key, val]) =>
        <StepValueTile src={'../img/racks/capacity-' + key + '.png'} value={key} label={key + ' filières'} price={getPriceLabel(key)} onSelect={selectValue} key={'cap' + key} />
      )}
    </StepGeneric>
  );
}
