import React from 'react';
import { Container } from 'react-bootstrap';
import { Route, Switch, Redirect, useRouteMatch } from 'react-router';
import Racks from './racks/Racks';
import './ProductsPage.scss';

function ProductsPage() {
  let { path } = useRouteMatch();

  return (
    <Container className="page order">
      <Switch>
        <Route exact path={path}>
          {/* Create a page that let user pick a type of product */}
          <Redirect to='produits/supportFilieres' />
        </Route>
        <Route path={`${path}/supportFilieres`}>
          <Racks />
        </Route>
      </Switch>
    </Container>
  )
}

export default ProductsPage;
