import React, { useState } from 'react';
import { Row, Col, Image, Button, Form, Alert, Spinner } from 'react-bootstrap';
import { useRouteMatch } from 'react-router';
import { Optionnal } from '../../../components/Optionnal';
import { logEvent } from '../../../components/TawkTo';
import { useFormSubmit } from '../../../utils/FormSubmit';
import { ConfigureRack, getNewConfiguration as rGetNewConfiguration, validConfiguration, RackConfiguration, getTotalPrice as getRackPrice, getPartNumber } from './ConfigureRack';
import { ConfigureShippingPayment, getNewConfiguration as spGetNewConfiguration, getTotalPrice, ShippingPaymentConfiguration } from './ConfigureShippingPayment';
import './Racks.scss';

function Racks() {
  let { path, url } = useRouteMatch();

  const [products, setProducts] = useState<RackConfiguration[]>([rGetNewConfiguration()]);
  const [shippingPayment, setShippingPayment] = useState<ShippingPaymentConfiguration>(spGetNewConfiguration());
  const [orderState, sendFormOrder, sendOrder] = useFormSubmit('New order !');

  function onProductChange(index: number, product: RackConfiguration) {
    const newProducts = [...products];
    newProducts[index] = product;
    setProducts(newProducts);

    if (validConfiguration(product)) {
      logEvent('productChange', {
        nbProducts: newProducts.length,
        basket: newProducts.map(getPartNumber).join(', '),
        totalProduct: getProductsPrice(newProducts)
      });
    }
  }

  function onAddProduct() {
    setProducts([...products, rGetNewConfiguration()]);
  }

  function getProductsPrice(products: RackConfiguration[]): number {
    return products.reduce((p, product) => p + (validConfiguration(product) ? getRackPrice(product) : 0), 0);
  }

  function onShippingChange(conf: ShippingPaymentConfiguration) {
    setShippingPayment(conf);

    logEvent('shippingChange', {
      products: products.map(getPartNumber).join(', '),
      shipping: conf.shipping ? conf.shipping[0] : '',
      payment: conf.payment ? conf.payment[0] : '',
      totalPrice: getTotalPrice(getProductsPrice(products), products.filter(p => validConfiguration(p)).length, conf) + '€',
      customer: conf.customer ? (conf.customer.firstName + ' ' + conf.customer.name + ' - ' + conf.customer.email) : ''
    });
  }

  function confirmOrder(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();

    const data = {
      products: products.map(getPartNumber).join(', '),
      shipping: shippingPayment.shipping ? shippingPayment.shipping[0] : '',
      payment: shippingPayment.payment ? shippingPayment.payment[0] : '',
      totalPrice: getTotalPrice(getProductsPrice(products), products.filter(p => validConfiguration(p)).length, shippingPayment) + '€',
      customer: shippingPayment.customer ? (shippingPayment.customer.firstName + ' ' + shippingPayment.customer.name + ' - ' + shippingPayment.customer.email) : ''
    };

    logEvent('confirmOrder', data);
    sendOrder(data);
  }

  return (
    <div className="orderRacks">
      <Optionnal displayed={orderState.succeeded}>
        <Row className="my-5 justify-content-center">
          <Col md={10}>
            <Alert variant="success">Merci pour votre commande, nous reviendrons vers vous très vite avec les informations pour le paiement et organiser la livraison. Merci pour votre commande !</Alert>
          </Col>
        </Row>
      </Optionnal>
      <Optionnal displayed={!orderState.succeeded}>
        <Row className="mt-4">
          <Col md={5}>
            <Image src="../img/racks/racks.jpg" thumbnail alt="Support pour filières de Philips Pasta Maker" />
          </Col>
          <Col md={7}>
            <h2>Support pour filières</h2>
            <p>Disponible pour les machines <strong>Philips Pasta Maker Avance</strong> et <strong>Philips Pasta Maker Viva</strong>, en plusieurs capacités et en plusieurs couleurs.</p>
            <p>Faites vos choix pour configurer votre support puis passez commande ici-même.</p>
          </Col>
        </Row>
        <Row className="mt-3">
          <Col>
            {products.map((product, index) =>
              <ConfigureRack onChange={conf => onProductChange(index, conf)} onAdd={products.length - 1 === index ? onAddProduct : undefined} disabled={orderState.submitting} key={'product' + index} />
            )}
          </Col>
        </Row>
        <Optionnal displayed={products.some(prod => validConfiguration(prod))}>
          <Row className="mt-3 mb-4">
            <Col>
              <ConfigureShippingPayment basketTotal={getProductsPrice(products)} basketCount={products.filter(p => validConfiguration(p)).length} onChange={onShippingChange} disabled={orderState.submitting} />
            </Col>
          </Row>
          <Optionnal displayed={getTotalPrice(getProductsPrice(products), products.filter(p => validConfiguration(p)).length, shippingPayment) > 0}>
            <Row className="mt-3 mb-5">
              <Col>
                <p>En cliquant sur le bouton de validation, nous serons informé de votre commande. Je reviendrais alors vers vous par e-mail pour organiser la livraison et le paiement suivant les choix que vous aurez fait sur cette page. </p>
                <Form onSubmit={confirmOrder}>
                  <Button className="validate" type="submit" variant="primary" size="lg" block disabled={orderState.submitting}>
                    <Optionnal displayed={orderState.submitting}><Spinner as="span" className="waiting" animation="border" role="status" aria-hidden="true" /></Optionnal>
                    Valider ma commande
                  </Button>
                </Form>
              </Col>
            </Row>
          </Optionnal>
        </Optionnal>
      </Optionnal>
    </div>
  )
}

export default Racks;
