import React, { useState } from "react";
import { StepShipping } from "./steps/StepShipping";
import { StepPayment } from "./steps/StepPayment";
import { CustomerInformations, StepCustomerInformations } from "./steps/StepCustomerInformations";
import { StepPrice } from "./steps/StepPrice";

export interface ShippingPaymentConfiguration {
  shipping?: [string, number]
  payment?: [string, number]
  customer?: CustomerInformations
}

export function getNewConfiguration(): ShippingPaymentConfiguration {
  return {
    shipping: undefined,
    payment: undefined,
    customer: undefined
  };
}

export function getTotalPrice(basketTotal: number, basketCount: number, conf: ShippingPaymentConfiguration): number {
  if (!conf.shipping || !conf.payment || !conf.customer) return 0;

  const fixAmount = basketTotal + conf.shipping[1] + (basketCount - 1);
  const paymentMultiplier = 1 + (conf.payment[1] / 100);
  return Math.round(fixAmount * paymentMultiplier * 10) / 10;
}

export function ConfigureShippingPayment(props: {
  basketTotal: number,
  basketCount: number,
  onChange: (conf: ShippingPaymentConfiguration) => void
  disabled?: boolean
}) {
  const [stepIndex, setStepIndex] = useState(0);
  const [config, setConfig] = useState<ShippingPaymentConfiguration>(getNewConfiguration());

  function updateConfig(attribute: keyof ShippingPaymentConfiguration, value: any) {
    const newConfig = { ...config, [attribute]: value };
    setConfig(newConfig);
    props.onChange(newConfig);
  }

  return (
    <div className="configureShippingPayment clearfix">
      <div className="myAccordion w-100 border border-info">
        <StepShipping expanded={stepIndex === 0} onSelected={() => props.disabled || setStepIndex(0)} onNext={() => props.disabled || setStepIndex(1)} onChange={val => updateConfig('shipping', val)} />
        <StepPayment displayed={stepIndex >= 1} expanded={stepIndex === 1} onSelected={() => props.disabled || setStepIndex(1)} onNext={() => props.disabled || setStepIndex(2)} onChange={val => updateConfig('payment', val)} />
        <StepCustomerInformations displayed={stepIndex >= 2} expanded={stepIndex === 2} onSelected={() => props.disabled || setStepIndex(2)} onNext={() => props.disabled || setStepIndex(3)} onUserChange={val => updateConfig('customer', val)}/>
        <StepPrice displayed={stepIndex === 3} total={getTotalPrice(props.basketTotal, props.basketCount, config)} />
      </div>
    </div>
  );
}