import React from 'react';
import { Optionnal } from '../../../../components/Optionnal';

export interface MinimunStepGenericProps {
  displayed?: boolean,
  expanded: boolean,
  onSelected?: () => void,
  onNext?: () => void
}

interface StepGenericProps extends MinimunStepGenericProps {
  stepClassName: string
  typeLabel: string
  introLabel: string
  valueLabel: () => string
  priceLabel?: () => string
  onChange?: (value: [val: string, price: number]) => void
}

export function StepGeneric(props: React.PropsWithChildren<StepGenericProps>) {
  return (
    <Optionnal displayed={props.displayed !== false}>
      <div className={'step ' + props.stepClassName + ' border border-info ' + (props.expanded ? 'expanded' : 'collapsed bg-light')}>
        <div className={'header py-2 px-2 ' + (props.expanded ? '' : 'bg-light')} onClick={props.onSelected}>
          <span className="type">{props.expanded ? props.introLabel : props.typeLabel + ' :'}</span>&nbsp;
          <span className="value text-muted">{props.expanded ? '' : props.valueLabel()}</span>
          <Optionnal displayed={!props.expanded && props.priceLabel !== undefined}><span className="price float-right">{props.priceLabel ? props.priceLabel() : ''}</span></Optionnal>
        </div>
        <Optionnal displayed={props.expanded}>
          <div className="content border-top border-info p-2">
            {props.children}
          </div>
        </Optionnal>
      </div>
    </Optionnal>
  );
}