
import React, { useState } from 'react';
import { Optionnal } from '../../../components/Optionnal';
import { StepMachine } from './steps/StepMachine';
import { StepCapacity } from './steps/StepCapacity';
import { StepColor } from './steps/StepColor';
import { StepPrice } from './steps/StepPrice';
import { Button } from 'react-bootstrap';

export interface RackConfiguration {
  machine: [string, number],
  capacity: [string, number],
  color: [string, number]
}

export function getNewConfiguration(): RackConfiguration {
  return {
    machine: ['', 0],
    capacity: ['', 0],
    color: ['', 0],
  };
}

export function getPartNumber(config: RackConfiguration): string {
  return config.machine[0] + '-' + config.capacity[0] + '-' + config.color[0];
}

export function validConfiguration(config: RackConfiguration): boolean {
  return config.machine[0] !== '' && config.capacity[0] !== '' && config.color[0] !== '';
}

export function getTotalPrice(config: RackConfiguration): number {
  return Object.values(config).reduce((p, c) => p + c[1], 0);
}

export function ConfigureRack(props: {
  onChange: (conf: RackConfiguration) => void
  onAdd?: () => void
  disabled?: boolean
}) {
  const [stepIndex, setStepIndex] = useState(0);
  const [product, setProduct] = useState<RackConfiguration>(getNewConfiguration());

  function updateProduct(attribute: keyof RackConfiguration, value: [string, number]) {
    const newProduct = { ...product, [attribute]: value };
    setProduct(newProduct);
    props.onChange(newProduct);
  }

  return (
    <div className="configureRack clearfix">
      <div className="myAccordion w-100 border border-info">
        <StepMachine expanded={stepIndex === 0} onSelected={() => props.disabled || setStepIndex(0)} onNext={() => props.disabled || setStepIndex(1)} onChange={val => updateProduct('machine', val)} />
        <StepCapacity displayed={stepIndex >= 1} expanded={stepIndex === 1} onSelected={() => props.disabled || setStepIndex(1)} onNext={() => props.disabled || setStepIndex(2)} onChange={val => updateProduct('capacity', val)} />
        <StepColor displayed={stepIndex >= 2} expanded={stepIndex === 2} onSelected={() => props.disabled || setStepIndex(2)} onNext={() => props.disabled || setStepIndex(3)} onChange={val => updateProduct('color', val)} />
        <StepPrice displayed={stepIndex === 3} total={getTotalPrice(product)} />
      </div>
      <Optionnal displayed={stepIndex === 3 && props.onAdd !== undefined}><Button className="my-2 float-right" variant="primary" onClick={props.onAdd} disabled={props.disabled}>Ajouter un autre support à ma commande</Button></Optionnal>
    </div>
  );
}