import React from 'react';
import { Container, Row, Col, Image } from 'react-bootstrap';
import './HomePage.scss';

function HomePage() {
  return (
    <Container className="page home">
      <Row className="about-us mt-5">
        <Col md={12} className="section-heading text-center">
          <h2>À propos de nous</h2>
          <h3 className="my-5 mx-auto">Passionné par le fait-main, nous concevons des objets pour la maison, souvent fabriqués à l'aide d'une imprimante 3D.</h3>
        </Col>
      </Row>
      <Row>
        <Col md={7}>
          <Image src="img/intro.jpg" thumbnail alt="Support Philips Pasta Maker Avance 32 filières" />
        </Col>
        <Col md={5}>
          <h2>Les supports pour filières</h2>
          <p>Notre plus grand succès aujourd'hui: un support pour filières pour machines à pâtes.</p>
          <p>Il est disponible pour les machines <strong>Philips Pasta Maker Avance</strong> et <strong>Philips Pasta Maker Viva</strong>, en plusieurs capacités et en plusieurs couleurs.</p>
          <p>De nouveaux modèles sont en préparation, restez à l'écoute !</p>
        </Col>
      </Row>
    </Container>
  )
}

export default HomePage;
