import React, { useState } from 'react';
import { MinimunStepGenericProps, StepGeneric } from './StepGeneric';
import { StepValueTile } from './StepValueTile';

const MACHINES = new Map<string, { label: string, picture: string }>([
  ['avance', { label: 'Philips Pasta Maker Avance', picture: 'machine-avance.jpg' }],
  ['viva', { label: 'Philips Pasta Maker Viva', picture: 'machine-viva.png' }],
]);

interface StepMachineProps extends MinimunStepGenericProps {
  onChange?: (value: [val: string, price: number]) => void
}

export function StepMachine(props: StepMachineProps) {
  const [value, setValue] = useState<string>('');

  function selectValue(value: string): void {
    setValue(value);
    if (props.onChange)
      props.onChange([value, 0]);
    if (props.onNext)
      props.onNext();
  }

  function getValueLabel(): string {
    const machine = MACHINES.get(value);
    return machine ? machine.label : '';
  }

  return (
    <StepGeneric {...props} stepClassName='machine' typeLabel='Machine' introLabel='Machine utilisée' valueLabel={getValueLabel}>
      {Array.from(MACHINES, ([key, val]) =>
        <StepValueTile src={'../img/racks/' + val.picture} label={val.label} value={key} onSelect={selectValue} key={'machine-' + key} />
      )}
    </StepGeneric>
  );
}
