import { useState } from 'react';

interface FormSubmitState {
  succeeded: boolean,
  error: string,
  submitting: boolean
}

export function useFormSubmit(subject: string, destinationEmail: string = 'f4b1f3525078ed28a2659dbb69c5a942'): [FormSubmitState, (e: React.FormEvent<HTMLFormElement>) => void, (data: object) => void] {
  const [formState, setFormState] = useState<FormSubmitState>({
    succeeded: false,
    error: '',
    submitting: false
  });

  function sendMail(data: object): void {
    setFormState({ ...formState, submitting: true });

    fetch('https://formsubmit.co/ajax/' + destinationEmail, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify({ ...data, _subject: subject })
    }).then(() => {
      setFormState({ ...formState, succeeded: true, submitting: false });
    }).catch(error => {
      setFormState({ ...formState, error: error, submitting: false });
    });
  }

  function handleSubmit(event: React.FormEvent<HTMLFormElement>): void {
    event.preventDefault();

    const formData = new FormData(event.target as HTMLFormElement);

    let data: Record<string, string | string[]> = {};
    formData.forEach((value, key) => {
      if (!Reflect.has(data, key)) {
        data[key] = value.toString();
        return;
      }
      if (!Array.isArray(data[key])) {
        data[key] = [data[key] as string];
      }
      (data[key] as string[]).push(value.toString());
    });

    sendMail(data);
  }

  return [formState, handleSubmit, sendMail];
}