import React, { useState } from 'react';
import { Form, InputGroup } from 'react-bootstrap';
import { MinimunStepGenericProps, StepGeneric } from './StepGeneric';

interface ShippingMode {
  label: string,
  price: number
}

const ShippingModesByCountries = new Map<string, { label: string, modes: { [id: string]: ShippingMode } }>([
  ['fr', {
    label: 'France', modes: {
      none: { label: 'En main propre à Gentilly (94250)', price: 0 },
      mrelay_fr: { label: 'Mondial Relay', price: 5.5 },
      laposte_fr: { label: 'Colissimo - Livraison en 2 jours', price: 9 }
    }
  }],
  ['be', {
    label: 'Belgique', modes: {
      mrelay_be: { label: 'Mondial Relay ', price: 5.5 },
      laposte_be: { label: 'Colissimo International', price: 17 }
    }
  }],
  ['lu', {
    label: 'Luxembourg', modes: {
      mrelay_lu: { label: 'Mondial Relay', price: 6 },
      laposte_lu: { label: 'Colissimo International', price: 17 }
    }
  }],
  ['de', {
    label: 'Allemagne', modes: {
      mrelay_de: { label: 'Mondial Relay', price: 10.5 },
      laposte_de: { label: 'Colissimo International', price: 17 }
    }
  }],
  ['at', {
    label: 'Autriche', modes: {
      mrelay_at: { label: 'Mondial Relay', price: 12 },
      laposte_at: { label: 'Colissimo International', price: 17 }
    }
  }],
  ['es', {
    label: 'Espagne', modes: {
      mrelay_es: { label: 'Mondial Relay', price: 8 },
      laposte_es: { label: 'Colissimo International', price: 17 }
    }
  }],
  ['it', {
    label: 'Italie', modes: {
      mrelay_it: { label: 'Mondial Relay', price: 12 },
      laposte_it: { label: 'Colissimo International', price: 17 }
    }
  }],
  ['nl', {
    label: 'Pays Bas', modes: {
      mrelay_nl: { label: 'Mondial Relay', price: 6.6 },
      laposte_nl: { label: 'Colissimo International', price: 17 }
    }
  }],
  ['pt', {
    label: 'Portugal', modes: {
      mrelay_pt: { label: 'Mondial Relay', price: 12 },
      laposte_pt: { label: 'Colissimo International', price: 17 }
    }
  }],
]);

interface StepShippingProps extends MinimunStepGenericProps {
  onChange?: (value: [val: string, price: number]) => void
}

export function StepShipping(props: StepShippingProps) {
  const [country, setCountry] = useState<string>('fr');
  const [mode, setMode] = useState<string>('mrelay_fr');

  function selectCountry(value: string): void {
    const countryObj = ShippingModesByCountries.get(value);
    if (!countryObj) return;

    const firstModeKey = Object.keys(countryObj.modes)[0];

    setCountry(value);
    setMode(firstModeKey);

    if (props.onChange)
      props.onChange([firstModeKey, countryObj.modes[firstModeKey].price]);
  }

  function selectMode(value: string): void {
    const countryObj = ShippingModesByCountries.get(country);
    if (!countryObj) return;

    const modeObj = countryObj.modes[value];
    if (!modeObj) return;

    setMode(value);

    if (props.onChange)
      props.onChange([value, modeObj.price]);

    if (props.onNext)
      props.onNext();
  }

  function getValueLabel(): string {
    const countryObj = ShippingModesByCountries.get(country);
    if (!countryObj) return '';

    const modeObj = countryObj.modes[mode];
    if (!countryObj) return '';

    return modeObj.label;
  }

  function getPriceLabel(): string {
    const countryObj = ShippingModesByCountries.get(country);
    if (!countryObj) return '';

    const modeObj = countryObj.modes[mode];
    if (!countryObj) return '';

    return modeObj.price > 0 ? ('+' + modeObj.price + '€') : 'Gratuit';
  }

  const availableModes = ShippingModesByCountries.get(country)?.modes || {};

  return (
    <StepGeneric {...props} stepClassName='shipping' typeLabel='Expédition' introLabel="Mode d'expédition" valueLabel={getValueLabel} priceLabel={getPriceLabel}>
      <Form inline className="justify-content-center">
        <InputGroup className="mr-3">
          <InputGroup.Prepend>
            <InputGroup.Text>Pays de destination</InputGroup.Text>
          </InputGroup.Prepend>
          <Form.Control as="select" onChange={event => selectCountry(event.target.value)} value={country}>
            {Array.from(ShippingModesByCountries).map(([countryCode, shippingModes]) =>
              <option key={'country-' + countryCode} value={countryCode}>{shippingModes.label}</option>
            )}
          </Form.Control>
        </InputGroup>
        <InputGroup>
          <InputGroup.Prepend>
            <InputGroup.Text>Mode d'envoi</InputGroup.Text>
          </InputGroup.Prepend>
          <Form.Control as="select" onChange={event => selectMode(event.target.value)} value={country}>
            <option className="placeholder">-</option>
            {Object.entries(availableModes).map(([id, mode]) =>
              <option key={'mode-' + id} value={id}>{mode.label} - {mode.price}€</option>
            )}
          </Form.Control>
        </InputGroup>
        <Form.Text className="text-muted float-right">
          Les supports supplémentaires au premier seront envoyés dans le même colis pour 1€ de plus.
        </Form.Text>
      </Form>
    </StepGeneric>
  );
}
