import React from 'react';
import { Image } from 'react-bootstrap';
import { Optionnal } from '../../../../components/Optionnal';

export interface StepValueTileProps {
  label?: string
  value: string
  src: string
  price?: string
  onSelect: (value: string) => void
}

export function StepValueTile(props: StepValueTileProps) {
  const label = '' + (props.label || props.value);
  return (
    <figure className="figure" onClick={() => props.onSelect(props.value)}>
      <Image src={props.src} thumbnail alt={label} />
      <Optionnal displayed={props.price !== undefined && props.price !== ''}><span className="price">{props.price}</span></Optionnal>
      <figcaption className="figure-caption">{label}</figcaption>
    </figure>
  );
}