import React, { useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { MinimunStepGenericProps, StepGeneric } from './StepGeneric';

export interface CustomerInformations {
  firstName: string
  name: string
  email: string
}

interface StepCustomerInformationsProps extends MinimunStepGenericProps {
  onUserChange?: (value?: CustomerInformations) => void
}

export function StepCustomerInformations(props: StepCustomerInformationsProps) {
  const [customerInformations, setCustomerInformations] = useState<CustomerInformations>({
    firstName: '',
    name: '',
    email: ''
  });

  function getValueLabel(val?: any): string {
    return customerInformations.firstName + ' ' + customerInformations.name + ' (' + customerInformations.email + ')';
  }

  function onSubmit() {
    if (props.onUserChange)
      props.onUserChange(customerInformations);

    if (props.onNext)
      props.onNext();
  }

  return (
    <StepGeneric {...props} stepClassName='customerInformations' typeLabel='Identité' introLabel="Informations personnelles" valueLabel={getValueLabel}>
      <Form inline className="justify-content-around flex-fill" onSubmit={onSubmit}>
        <Form.Control name="firstName" placeholder="Prenom" value={customerInformations.firstName} onChange={event => setCustomerInformations({ ...customerInformations, firstName: event.target.value })} />
        <Form.Control name="name" placeholder="Nom" value={customerInformations.name} onChange={event => setCustomerInformations({ ...customerInformations, name: event.target.value })} />
        <Form.Control type="email" name="email" value={customerInformations.email} placeholder="E-mail" onChange={event => setCustomerInformations({ ...customerInformations, email: event.target.value })} />
        <Button type="submit" variant="primary" disabled={!customerInformations.firstName || !customerInformations.name || !customerInformations.email}>Ok</Button>
      </Form>
    </StepGeneric>
  );
}
