import React, { useState } from 'react';
import { Form, ToggleButton, ToggleButtonGroup } from 'react-bootstrap';
import { MinimunStepGenericProps, StepGeneric } from './StepGeneric';

interface PaymentMode {
  label: string,
  details: string,
  price: number
}

const PaymentModes = new Map<string, PaymentMode>([
  ['transfert', { label: 'Virement bancaire', details: 'Paiement en quelques jours, sûr et sécurisé.', price: 0 }],
  ['paypal_friends', { label: 'Paypal - entre amis', details: 'Paiement instantané, sûr et sécurisé.', price: 0 }],
  ['paypal_goods', { label: 'Paypal  - biens & services', details: 'Paiement instantané, sûr et sécurisé avec assurance acheteur.', price: 5 }],
]);

interface StepPaymentProps extends MinimunStepGenericProps {
  onChange?: (value: [val: string, price: number]) => void
}

export function StepPayment(props: StepPaymentProps) {
  const [mode, setMode] = useState<string>('');

  function selectMode(value: string): void {
    const modeObj = PaymentModes.get(value);
    if (!modeObj) return;

    setMode(value);

    if (props.onChange)
      props.onChange([value, modeObj.price]);

    if (props.onNext)
      props.onNext();
  }

  function getValueLabel(val?: PaymentMode): string {
    const modeObj = val || PaymentModes.get(mode);
    if (!modeObj) return '';

    return modeObj.label;
  }

  function getPriceLabel(val?: PaymentMode): string {
    const modeObj = val || PaymentModes.get(mode);
    if (!modeObj) return '';

    return modeObj.price > 0 ? '+' + modeObj.price + '%' : 'Gratuit';
  }

  return (
    <StepGeneric {...props} stepClassName='payment' typeLabel='Paiement' introLabel="Mode de paiement" valueLabel={getValueLabel} priceLabel={getPriceLabel}>
      <Form inline>
        <ToggleButtonGroup className="justify-content-center" type="radio" name="options" onChange={selectMode}>
          {Array.from(PaymentModes, ([key, val]) =>
            <ToggleButton className="flex-column px-4" value={key} key={'pay-' + key}>
              <span className="title">{getValueLabel(val)}</span>
              <span className="price mb-2">{getPriceLabel(val)}</span>
              <span className="details font-weight-light">{val.details}</span>
            </ToggleButton>
          )}
        </ToggleButtonGroup>
      </Form>
    </StepGeneric>
  );
}
