import React from 'react';
import { Container, Form, Button, Row, Col, Alert } from 'react-bootstrap';
import { Optionnal } from '../../components/Optionnal';
import { useFormSubmit } from '../../utils/FormSubmit';
import './ContactPage.scss';

function ContactPage() {
  const [state, handleSubmit] = useFormSubmit('Message from contact page');

  return (
    <Container className="page contact">
      <Optionnal displayed={state.succeeded}>
        <Row className="my-5 justify-content-center">
          <Col md={10}>
            <Alert variant="success">Merci pour votre message, nous vons répondrons par mail le plus rapidement possible !</Alert>
          </Col>
        </Row>
      </Optionnal>
      <Optionnal displayed={!state.succeeded}>
        <Row className="my-5">
          <Col md={12}>
            <p>Vous avez une question ?</p>
            <p>N'hésitez pas à nous envoyer un message, nous vons répondrons par e-mail le plus rapidement possible !</p>
          </Col>
        </Row>
        <Row className="justify-content-center">
          <Col md={8} className="">
            <Form onSubmit={handleSubmit}>
              <Row>
                <Col>
                  <Form.Group>
                    <Form.Control name="nom" placeholder="Nom" />
                  </Form.Group>
                </Col>
                <Col>
                  <Form.Group>
                    <Form.Control name="prenom" placeholder="Prenom" />
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col>
                  <Form.Group>
                    <Form.Control type="email" name="email" placeholder="Adresse e-mail" />
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col>
                  <Form.Group>
                    <Form.Control as="textarea" name="message" rows={5} placeholder="Message" />
                  </Form.Group>
                </Col>
              </Row>
              <Button variant="primary" type="submit" disabled={state.submitting}>Envoyer</Button>
            </Form>
          </Col>
        </Row>
      </Optionnal>
    </Container>
  )
}

export default ContactPage;
