import React from 'react';
import { StepGeneric } from './StepGeneric';

interface StepPriceProps {
  displayed?: boolean
  total: number
}

export function StepPrice(props: StepPriceProps) {
  return (
    <StepGeneric displayed={props.displayed} expanded={false} stepClassName='price' typeLabel='Total' introLabel='' valueLabel={() => props.total + '€'}></StepGeneric>
  );
}
