import React, { useState } from 'react';
import { MinimunStepGenericProps, StepGeneric } from './StepGeneric';
import { StepValueTile } from './StepValueTile';

const COLORS = new Map<string, { label: string, price: number }>([
  ['white', { label: 'Blanc', price: 0 }],
  ['black', { label: 'Noir', price: 1 }],
  ['nightblue', { label: 'Bleu nuit', price: 1 }],
  ['grey', { label: 'Gris', price: 1 }],
  ['red', { label: 'Rouge', price: 1 }],
  ['skyblue', { label: 'Bleu ciel', price: 2 }],
  ['turquoiseblue', { label: 'Bleu turquoise', price: 2 }],
  ['bronze', { label: 'Bronze', price: 3 }],
  ['pastelblue', { label: 'Bleu pastel', price: 3.5 }],
  ['pastelgreen', { label: 'Vert pastel', price: 3.5 }],
  ['pastelpink', { label: 'Rose pastel', price: 3.5 }]
]);

interface StepColorProps extends MinimunStepGenericProps {
  onChange?: (value: [val: string, price: number]) => void
}

export function StepColor(props: StepColorProps) {
  const [value, setValue] = useState<string>('');

  function selectValue(value: string): void {
    const val = COLORS.get(value);
    if (!val) return;

    setValue(value);
    if (props.onChange)
      props.onChange([value, val.price]);
    if (props.onNext)
      props.onNext();
  }

  function getValueLabel(): string {
    return '' + COLORS.get(value)?.label;
  }

  function getPriceLabel(valueKey: string = value): string {
    const val = COLORS.get(valueKey);
    return val && val.price > 0 ? '+' + val.price + '€' : '';
  }

  return (
    <StepGeneric {...props} stepClassName='color' typeLabel='Couleur' introLabel='Couleur du support' valueLabel={getValueLabel} priceLabel={getPriceLabel}>
      {Array.from(COLORS, ([key, val]) =>
        <StepValueTile src={'../img/racks/color-' + key + '.png'} label={val.label} price={getPriceLabel(key)} value={key} onSelect={selectValue} key={'col' + key} />
      )}
    </StepGeneric>
  );
}
